/* thin */
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 100;
  src: local("Poppins Thin"), local("Poppins-Thin"),
    url("/plugins/Greenwing/src/fonts/poppins/Poppins-Thin.woff2")
      format("woff2"),
    url("/plugins/Greenwing/src/fonts/poppins/Poppins-Thin.woff")
      format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: italic;
  font-weight: 100;
  src: local("Poppins Thin Italic"), local("Poppins-ThinItalic"),
    url("/plugins/Greenwing/src/fonts/poppins/Poppins-ThinItalic.woff2")
      format("woff2"),
    url("/plugins/Greenwing/src/fonts/poppins/Poppins-ThinItalic.woff")
      format("woff");
}

